	module.exports = {"data":{"markdownRemark":{"id":"/home/gabriele/Mannequin/src/Html/docs/components.md absPath of file >>> MarkdownRemark","html":"<p>Component information is stored in YAML files that live alongside the HTML files.</p>\n<h3 id=\"name\"><a href=\"#name\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Name</h3>\n<p>The component name is specified under the <code>name</code> key.  Example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token key atrule\">name</span><span class=\"token punctuation\">:</span> Super Button\n</code></pre>\n      </div>\n<h3 id=\"group\"><a href=\"#group\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Group</h3>\n<p>The component's group determines where it displays in the UI.  The group can be nested using the <code>></code> character.  It is specified beneath the <code>group</code> key.  Example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token key atrule\">group</span><span class=\"token punctuation\">:</span> Atoms<span class=\"token punctuation\">></span>Buttons\n</code></pre>\n      </div>","headings":[{"value":"Name","depth":3},{"value":"Group","depth":3}],"frontmatter":{"title":"Components","description":"Reference and documentation on HTML components in Mannequin."},"fields":{"ghEditUrl":"https://github.com/rigoblock/Html/docs/components.md","extension":"Html"}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/extensions/html/","menuTitle":"Overview"},"frontmatter":{"title":"Html"},"id":"/home/gabriele/Mannequin/src/Html/README.md absPath of file >>> MarkdownRemark"}},{"node":{"fields":{"slug":"/extensions/html/components/","menuTitle":"Components"},"frontmatter":{"title":"Components"},"id":"/home/gabriele/Mannequin/src/Html/docs/components.md absPath of file >>> MarkdownRemark"}},{"node":{"fields":{"slug":"/extensions/html/configuration/","menuTitle":"Configuration"},"frontmatter":{"title":"Configuration"},"id":"/home/gabriele/Mannequin/src/Html/docs/configuration.md absPath of file >>> MarkdownRemark"}},{"node":{"fields":{"slug":"/extensions/html/cli/","menuTitle":"CLI"},"frontmatter":{"title":"CLI"},"id":"/home/gabriele/Mannequin/src/Html/docs/cli.md absPath of file >>> MarkdownRemark"}},{"node":{"fields":{"slug":"/extensions/html/support/","menuTitle":"FAQ & Support"},"frontmatter":{"title":"FAQ & Support"},"id":"/home/gabriele/Mannequin/src/Html/docs/support.md absPath of file >>> MarkdownRemark"}}]}},"pathContext":{"id":"/home/gabriele/Mannequin/src/Html/docs/components.md absPath of file >>> MarkdownRemark","extension":"Html"}}

